body{
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  box-sizing: border-box;
  background-color: black;
  color: white;
}

#root{
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  --yellow: #fabc03;
  --red: #ec1f24;  
}

p{
  margin: 0;
}

@font-face {
  font-family: Dosis;
  src: url(Dosis-VariableFont_wght.ttf);
}

/* ----------- DESKTOP -----------*/
@media (min-width: 768px){
  .page{
    padding: 0 15px;
    flex-grow: 1;
    max-width: 1600px;
    width: 100%;
    box-sizing: border-box;
  }
  
  .about{
    margin-top: 25px;
    display: flex;
    align-items: center;
    gap: 30px;
  }

  .about-right{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }

  .home-pic{
    width: 60%;
  }

  .home-text{
    font-size: 25px;
    text-align: center;
    color: var(--yellow);
    width: 100%;
    font-weight: 500;
    font-style: italic;
    font-family: Dosis;
  }

  .venues{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    gap: 50px;
    justify-content: center;
  }

  .venue{
    max-width: 10%;
    object-fit: contain;
  }

  .venue-long{
    max-width: 25%;
    object-fit: contain;
  }

  .big-heading{
    font-size: 40px;
    font-weight: bold;
    margin: 25px 0;
    color: var(--yellow);
    font-family: Dosis;
  }
  
  .heading{
    font-weight: bold;
    font-size: 20px;
    font-family: Dosis;
    color: var(--yellow);
  }
  
  .pic{
    max-height: 350px;
    border: 1px solid gray;
  }

  .show-pic{
    max-width: 100%;
    border: 1px solid gray;
  }
  
  .divider{
    background-color: var(--red);
    height: 1px;
    width: 100%;
    margin: 15px 0;
  }

  .team-links{
    padding-left: 4px;
  }

  .team-links a{
    color: white;
    display: block;
    color: var(--red);
    text-decoration: none;
  }
 
  .show-cards{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    grid-auto-rows: max-content;
    gap: 15px;
  }

  .presents-cards{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    grid-auto-rows: max-content;
    gap: 15px;
  }

  .gallery-pics{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 15px;
    margin-bottom: 15px;
  }

  .side-card{
    margin-bottom: 15px;
    display: flex;
    gap: 30px;
  }

  .card-text{
    text-align: justify;
    font-size: 25px;
    font-style: italic;
    font-family: Dosis;
  }
  
  .credit{
    font-size: 12px;
    color: #888888;
  }
  
  .credit-link{
    color: #888888;
  }
  
  .card-heading{
    font-weight: bold;
    font-size: 25px;
    font-family: "dosis";
    color: var(--yellow);
  }
  
  .card-body{
    display: flex;
    flex-direction: column;
  }

  .presents-body{
    align-items: center;
    margin-bottom: 30px;
  }

  .presents{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 15px;
  }

  .presents-main-pic{
    border: none;
  }

  .presents-pic{
    max-width: 100%;
  }
  
  .gray{
    color: #888888;
  }
  
  .photo{
    vertical-align: bottom;
  }

  .contact{
    width: 100%;
    max-width: 500px;
    margin: auto;
    text-align: center;
  }
  
  .socials{
    display: flex;
    justify-content: center;
    gap: 8px;
  }
  
  .socials-footer{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
  }
  
  .icon-big{
    max-height: 75px;
    transition: all .25s ease;
  }

  .icon-big:hover{
    opacity: .7;
  }
  
  .icon{
    height: 45px;
    vertical-align: bottom;
    transition: all .25s ease;
  }

  .icon:hover{
    opacity: .7;
  }
  
  .footer{
    background-color: black;
    color: white;
    display: flex;
    align-items: center;
    font-size: 12px;
    padding: 10px;
    justify-content: space-between;
    border-top: 1px solid white;
    width: 100%;
    box-sizing: border-box;
  }
  
  .to-eventbrite {
    background-color: var(--red);
    font-weight: bold;
    border: none;
    width: 100%;
    margin-top: 4px;
    margin-bottom: 15px;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
    transition: all .25s ease;
    box-sizing: border-box;
    font-family: Dosis;
    border-radius: 8px;
  }

  .to-eventbrite:hover{
    background-color: #ec1f22ce;
  }

}

